<template>
  <div class="select-overspread repair-bg auto" >
    <validator name="v" >
      <div class="auto repair-info-content compatible">
        <div class="row auto">
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group mg8">
            <label class="text-justify lb-left" for="f_install_num">实际可安装用户:</label>
            <input  type="text" class="form-control" id="f_install_num" v-model="model.f_install_num" >
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group" >
            <label class="text-justify lb-left" for="costmatter">缴费户数:</label>
            <input  type="text" class="form-control" id="costmatter" v-model="model.f_payfee_num" >
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" >用气类型:</label>
            <!--<v-select @selected="gastype_change" :disabled="!model.canedit" value="model.f_gas_type" :options='fgastype' placeholder='用气类型' close-on-select :value-single="true">-->
            <!--</v-select>-->
            <select v-model="model.f_gas_type"  class="form-control">
              <option v-for="option in fgastype">
                {{ option.label}}
              </option>
            </select>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" >勘察人员:</label>
            <!--<v-select @selected="surveyname_change" value="model.f_survey_name" :disabled="!model.canedit"  :options='fsurveyname' placeholder='勘察人员' close-on-select  :value-single="true">-->
            <!--</v-select>-->
            <select v-model="model.f_survey_name" class="form-control">
              <option v-for="option in fsurveyname">
                {{ option.label}}
              </option>
            </select>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left">勘查日期:</label>
            <datepicker
              :value.sync="model.f_survey_time"
              :disabled-days-of-Week="[]"
              :format="'yyyy-MM-dd HH:mm:ss'"
              v-model="model.f_survey_time"
              :show-reset-button="reset" placeholder="请输入勘察日期" style="margin-bottom: 8px"
             >
            </datepicker>
          </div>
          <div class="col-xs-12 col-sm-12 col-md-6 form-input-group">
            <input  type="checkbox" id="checkbox" v-model="model.f_isdesign"/>
            <label for="checkbox" class="text-justify lb-left">是否设计出图</label>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left">勘察意见:</label>
            <textarea class="form-control" cols="30" rows="4" style="resize: vertical;" v-model="model.f_survey_remarks" ></textarea>
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" for="f_survey_department">部门:</label>
            <input type="text" class="form-control" id="f_survey_department" v-model="model.f_survey_department" readonly  >
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="text-justify lb-left" for="f_survey_operator">执行人:</label>
            <input type="text" class="form-control" id="f_survey_operator" v-model="model.f_survey_operator" readonly  >
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="lb-left text-justify">分公司:</label>
            <input type="text" class="form-control"  v-model="model.f_filiale" readonly  >
          </div>
          <div class="col-xs-12 col-sm-6 col-md-6 form-input-group">
            <label class="lb-left text-justify">操作日期:</label>
            <input type="text" class="form-control"  v-model="model.f_survey_date" readonly  >
          </div>
        </div>
        <div style="text-align:center;margin-top: 5px;" v-if="model.canedit">
          <button  @click="save" type="button" class="btn btn-primary radius_btn" >保存</button>
          <button  @click="upload" type="button" class="btn btn-primary radius_btn" >上传</button>
        </div>
      </div>
    </validator>
  </div>
</template>
<script>
  import Vue from 'vue'
  import * as Util from '../Util'
  import co from 'co'

  let uploadApply = function * (self) {
    // self.model.f_app_upload = '已上传'
    // var data ={tablename:'t_apply',parameters:self.model}
    // var url = self.$androidUtil.getProxyUrl() + '/rs/logic/uploadapply'
    // console.log(`即将上传，url>>>> ${url}`)
    // let res = yield self.$resetpost(url, {data:data})
    // console.log(`上传返回json>>>>>>>>>>>> ${JSON.stringify(res)}`)
    // if(res.data.code != 200){
    //   self.$showMessage('上传失败！')
    //   return
    // }
    // data = {
    //   model: {
    //     f_processid: self.model.f_process_id
    //   },
    //   loginUser: {
    //     id: Vue.user.id,
    //     name: Vue.user.name,
    //     ename: Vue.user.ename
    //   },
    //   employeetemplate: self.employeetemplate,// 指派勘察人员
    //   subdep: '提交'
    // }
    // console.log(`即将请求后台修改流程，参数>>>> ${JSON.stringify(data)}`)
    // self.$resetpost(self.$androidUtil.getProxyUrl() + '/rs/logic/grantnext',{data: data}).then(() => {
    //   self.$showMessage('上传成功！')
    //   self.updateState()
    //   self.$parent.$back()
    // })


    // self.model.f_app_upload = '已上传'
    var param = {
      model: {
        f_processid: self.model.f_process_id
      },
      loginUser: {
        id: Vue.user.id,
        name: Vue.user.name,
        ename: Vue.user.ename
      },
      employeetemplate: self.employeetemplate,// 指派勘察人员
      subdep: '提交'
    }
    var data ={tablename:'t_apply',parameters:self.model,param: param}
    // var url = self.$androidUtil.getProxyUrl() + '/rs/logic/uploadapply'
    // console.log(`即将上传，url>>>> ${url}`)
    // let res = yield self.$resetpost(url, {data:data})
    let res = yield self.$androidUtil.bzLogic('uploadapply',data)
    console.log(`上传返回json>>>>>>>>>>>> ${JSON.stringify(res)}`)
    if(!res){
      self.$showMessage('上传失败！')
      return
    }
    if(res.code != 200){
      self.$showMessage('上传失败，原因:' + res.msg)
      return
    }
    self.$showMessage('上传成功！')
    self.updateState()
    self.$parent.$back()
  }

  export default {
    title: '现场勘探信息',
    props: {
      model: { // 传入一条报建信息
        type: Object
      }
    },
    data () {
      return {
        signType:{
          type: Boolean,
          default: true
        },
        fgastype: this.$appdata.getParam('用气类型'),
        fsurveyname : this.$appdata.getParam('勘查人员')
      }
    },
    methods: {
      upload() {
        var reg = /^\d+$/
        if (!reg.test(this.model.f_install_num)) {
          this.$showMessage('实际可安装用户必须为整数！')
          return false;
        }
        if (!reg.test(this.model.f_payfee_num)) {
          this.$showMessage('缴费户数必须为整数！')
          return false;
        }
        // this.model.f_gas_type = this.model.f_gas_type[0] ? this.model.f_gas_type[0] : ''
        this.model.f_app_upload = '已完成'
        console.log(`前台开始本地保存，传入数据json>>>>>>>${JSON.stringify(this.model)}`)
        let res = this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
        if(res.code != 200){
          this.$showMessage('保存失败,上传终止！')
          return
        }
        let gen = uploadApply(this)
        co(gen)
      },
      mute () {
        HostApp.mute()
      },
      // 上传完成后回写本地上传状态
      updateState(){
        console.log(`前台上传完成，开始回写本地上传状态`)
        this.model.f_app_upload = '已上传'
        this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
      },
      save() {
        var reg = /^\d+$/
        if (!reg.test(this.model.f_install_num)) {
          this.$showMessage('实际可安装用户必须为整数！')
          return false;
        }
        if (!reg.test(this.model.f_payfee_num)) {
          this.$showMessage('缴费户数必须为整数！')
          return false;
        }
        // this.model.f_gas_type = this.model.f_gas_type[0] ? this.model.f_gas_type[0] : ''
        this.model.f_app_upload = '已完成'
        console.log(`前台开始本地保存，传入数据json>>>>>>>${JSON.stringify(this.model)}`)
        let res = this.$androidUtil.bzLogic('saveapplyforapp', {parameters:this.model})
        console.log(`前台保存结束，返回res>>>>>>>${JSON.stringify(res)}`)
        if(res.code == 200){
          console.log(`前台保存成功!`)
          this.$showMessage('保存成功！')
        }else{
          console.log('前台保存失败')
          this.$showMessage('保存失败！')
        }
      },
      //查询勘探信息
      getFiledApply(){
        let response = this.$androidUtil.bzLogic('getFiledApply',{})
        this.$set('model.rows', response.data)
      }
    },
    ready(){
      console.log('--现场勘探--')

      this.model.f_survey_time = Util.toStandardTimeString()
      if(navigator.userAgent.match('iPad') || navigator.userAgent.match('iPhone')){
        this.signType = false
      }
      if(this.model && this.model.f_single_man){
        this.model.f_single_man = this.f_single_man
      }
      this.model.f_survey_date = Util.toStandardDateString()
      if(Vue.user){
        console.log('获取到Vue.user')
        this.model.f_survey_department = Vue.user.f_parentname
        this.model.f_survey_operator = Vue.user.name
      }
    },
    watch:{
      'model.f_isdesign'(val){
        // this.$showMessage(val)
        if(val){
          this.model.f_isdesign = 1
        }else{
          this.model.f_isdesign = 0
        }
      }
    }
  }
</script>
<style>
  .radius_btn{
    border-radius:  5px;
    margin-top: 0.1rem;
  }

  select{
    font-family: "微软雅黑";
    background: rgba(0,0,0,0);
    text-align: center;
    border-radius: 5px;
  }

</style>



